import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {
    newMember: {},
};

@observer
class TeamsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadTeam() {
		const { teamStore, userStore } = this.props.stores;
        await teamStore.load(this.props.team, false, { addData: ['dogs'] });
        const { team } = teamStore;
        await userStore.getWorkoutSummary({ team: team.id });
    }

    insertTeamMember = async () => {
        const { newMember } = this.state;
        const { teamStore } = this.props.stores;
		const { team } = teamStore;
        await teamStore.addMember(team.id, { ...newMember });
        this.setState({
            newMember: {},
        });
    }

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }

	componentDidMount() {
        this.loadTeam();
    }

	render() {
        const { newMember } = this.state;
		const { teamStore } = this.props.stores;
		const { team } = teamStore;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='d-flex justify-content-between mb-2'>
                    <div>
                        <button class='btn btn-link pl-0 fixed-top text-primary'
                            style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                            onClick={this.back}>
                            <i class='fas fa-arrow-circle-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'><Text id='teams.addmember.title'>Team - Legg til medlem</Text></h5>
                    </div>
                    <div>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                    </div>
                </div>

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5><Text id='teams.addmember.add-new'>Legg til nytt medlem i </Text>{team.name}</h5>
                        <div class='form-group'>
                            <label for='emailInput'><Text id='teams.addmember.email'>E-post</Text></label>
                            <input type='text' class='form-control' id='emailInput' aria-describedby='emailHelp'
                                onInput={linkState(this, 'newMember.email')}
                                value={newMember.email}
                            />
                            <small id='emailHelp' class='form-text text-muted'><Text id='teams.addmember.email-help'>E-postadressen til nytt medlem.</Text></small>
                        </div>
                        <div class='alert alert-warning' role='alert'>
                            <p>
                                <i class='fas fa-exclamation-circle float-left mr-3' />                    
                                <Text id='teams.addmember.info-1'>Medlemmer som legges til vil få full tilgang på hundene, treningene og løypene til dette teamet. </Text>
                            </p>
                            <p>
                                <Text id='teams.addmember.info-2'>Vi anbefaler at du kun legger til folk du stoler på :)</Text>
                            </p>
                        </div>
                        <button type='button' class='btn btn-primary'
                            onClick={this.insertTeamMember}
                        >
                            <Text id='teams.addmember.save-new'>Registrer nytt medlem</Text>
                        </button>

					</div>
				</div>
			</div>
		);
	}
}

export default TeamsDetail;
